:root {
  --link-color: #00acdc;
}

* {
  text-transform: uppercase;
  box-sizing: border-box;
  font-size: 12px;
}

html {
  font-family: Roboto, sans-serif;
  font-style: normal;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  width: 40px;
  height: 40px;
  display: block;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-weight: 500;
  display: flex;
  position: relative;
}

.nav__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link:hover, .is-active {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
